.sidebar {
	display: inline-block;
	width: 208px;
	min-width: 208px;
	transition: .3s linear;

	@media(max-width: 960px) {
		margin-left: -235px;
		background-color: $white+ed;
		overflow: overlay;
		border-radius: 0 8px 8px 0;
		box-shadow: 0px 0px 10px $gray-text;
		z-index: 999;
		position: fixed;
		height: 100%;
		top: 0;

		&.show {
			margin-left: 0;
			left: 0; } }

	& .btn {
		width: 100%;
		margin-bottom: 8px;

		@media(max-width: 960px) {
			width: 92%;
			margin: 0 4% 8px; } }

	&__close {
		opacity: 0;
		top: -256px;
		position: fixed;
		left: 220px;
		width: 32px;
		height: 32px;
		font-size: 32px;
		line-height: 30px;
		text-align: center;
		border-radius: 8px;
		box-shadow: 0px 0px 10px $gray-text;
		color: $green;
		transition: 1s linear;
		cursor: pointer;

		@media (max-width: 960px) {

			&[aria-expanded="true"] {
				opacity: 0.8;
				background-color: $white;
				z-index: 999;
				top: 0;

				&:hover {
					opacity: 1;
					transition: .3s linear; }

				&:active {
					background-color: $green;
					color: $white;
					opacity: 1;
					transition: .3s linear; } } } } }

.sidebar__left {
	border: 1px solid $gray-500;
	border-radius: 8px;
	overflow: hidden;
	margin-bottom: 40px;

	@media(max-width: 960px) {
		border-radius: 0 8px 8px 0;
		border: none;
		border-radius: 0; }

	&-group {
		border-bottom: 1px solid $gray-500; }

	&-name {
		padding: 16px;
		font-family: $lato-bold;
		font-size: 13px;
		letter-spacing: .52px;
		color: $body-color;
		font-weight: 500;

		@include arrow-top; }

	&-blockwrap {
		border-bottom: 1px solid $gray-500;
		overflow: hidden; }

	&-title {
		display: block;
		padding: 16px;
		cursor: pointer;
		font-family: $lato-bold;
		font-size: 13px;
		font-weight: 400;
		line-height: 16px;
		color: $gray-600;
		letter-spacing: .52px;
		border-top: 1px solid $gray-500;

		&:hover {
			color: $green; }

		&.dropdown {
			&:before {
				content: '+';
				position: absolute;
				right: 16px;
				font-size: 16px;
				font-weight: normal;
				font-family: serif; }


			&.open {
				&:before {
					content: ''; } } } }

	&-blocklist {
		overflow: overlay;

		&.dropdown {
			max-height: 320px; }

		.open + & {
			margin: 0 0 17px; } }

	&-list {
		padding: 0 16px 16px;
		font-family: Lato;
		font-size: 12px;
		font-weight: 300;
		margin: 0;

		& > li {
			list-style: none;
			padding: 8px 0; } }

	&-typelist {
		padding: 0 0 0 40px;

		& > li {
			list-style: disc;
			padding: 8px 0; } } }


.filter__range {
	margin: 0 16px 16px;
	padding: 0;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;

	& span {
		margin: 0 2px 0 0; }

	&-min,
	&-max {
		margin: 0;
		width: 64px;
		height: 24px;
		padding: 8px;
		margin: 0 6px 0 0;
		text-align: center;
		font-size: 13px;
		font-weight: 300;
		border-radius: 4px; }

	&-slider#slider-range {
		margin: 16px 6px 8px;
		flex: 1 1 100%;
		height: 8px;
		border: 1px solid $dark-main-color;
		border-radius: 10px;

		& .ui-widget-header {
			background-color: $dark-main-color; }

		& span {
			border-radius: 100%;
			height: 16px;
			width: 16px;
			border: 1px solid $dark-main-color;
			background-color: $white;
			cursor: pointer;

			&:focus {
				outline: none; } } }

	&-min {
		padding: 0; }

	&-max {
		margin: 0; } }
