.new-select:before {
				content: '';
				width: 0;
				height: 0;
				border: 5px solid transparent;
				border-top-color: #718391;
				position: absolute;
				right: 16px;
				top: 50%;
				text-align: center;
				font-size: 25px;
				cursor: pointer;
				margin-top: -1px;
				z-index: 1;
				transition: .2s linear;
}

