.new-select:after {
				content: '';
				height: 0;
				border: 5px solid transparent;
				border-top-color: #ffffff;
				display: inline-block;
				position: absolute;
				right: 16px;
				top: 50%;
				margin-top: -2px;
				z-index: 1;
				transition: .2s linear;
}

select.open:before,
