				border-width: 7px 7px 7px 0;
}

.tooltip.bs-tooltip-right .arrow::after, .tooltip.bs-tooltip-auto[x-placement^="right"] .arrow::after {
				content: "";
				position: absolute;
				border-color: transparent;
